import React, { useEffect, useRef } from "react";
import "./DragAndDrop.css"; // Import styles for the component
import { Image } from "react-bootstrap";
import useDroppableArea from "../../hooks/useDroppableArea";
// import usePlantDropableArea from "../../hooks/usePlantDropableArea";

const DragAndDropStandAlone = () => {
  const { initializeDroppableArea } = useDroppableArea();
  const hasInitializedDroppable = useRef(false);

  useEffect(() => {
    let attempts = 0;  
    const maxAttempts = 50; 

    const checkAndInitialize = () => {
      const droppableContainer = document.querySelector(".droppable-container");

      if (droppableContainer && !hasInitializedDroppable.current) {
        console.log("[DragAndDropStandAlone] ✅ Droppable container found. Initializing...");
        initializeDroppableArea();
        hasInitializedDroppable.current = true;
      } else {
        if (attempts >= maxAttempts) {
          console.error("[DragAndDropStandAlone] ❌ Max attempts reached. Stopping retries.");
          return; 
        }

        attempts++;
        console.warn(`[DragAndDropStandAlone] ❌ Droppable container not found yet. Retrying... (${attempts}/${maxAttempts})`);
        setTimeout(checkAndInitialize, 100); 
      }
    };

    checkAndInitialize();
  }, [initializeDroppableArea]);

  return (
    <>
      <div className="droppable-container mt-4">
        <Image
          style={{ width: "90px", height: "90px" }}
          src="https://thewhitechamaleon.github.io/testrapp/images/drag.png"
          alt="Data Collect"
          className="search-icon"
        />
        <span className="drag-and-drop-text">Drag and Drop</span>
        <div className="divider-container">
          <hr className="divider" />
          <span className="divider-text">or</span>
          <hr className="divider" />
        </div>
      </div>
    </>
  );
};

export default DragAndDropStandAlone;
